.accordion {
  width: 100%;
}

.accordion__item {
  p {
    margin: 0;
    margin-top: -5px;
    margin-bottom: 25px;
    color: $color-sapphire;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.4;

    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: opacity $trans-default, max-height $trans-default;
    will-change: opacity, max-height;
  }

  &:last-child {
    border-bottom: 1px solid $color-border-grey;
  }
}

.accordion__item-title[aria-open='true'] {
  span {
    background: $color-resolution-blue;
    &::after {
      width: 0;
    }
    &::before {
      background: $color-default-white;
    }
  }

  + p {
    opacity: 1;
    max-height: 9em;
    transition: all $trans-default;
    will-change: opacity, max-height;
  }
}

.accordion__item-title {
  &::-webkit-details-marker {
    display: none;
  }

  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 32px 0;
  outline: 0;
  list-style: none;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
  color: $color-sapphire;
  border-top: 1px solid $color-border-grey;
  cursor: pointer;

  span {
    position: relative;
    display: inline-block;
    width: 22px;
    height: 22px;
    padding: 11px;
    margin-left: auto;

    display: inline-block;
    width: 22px;
    height: 22px;

    border: 1px solid;
    border-radius: 22px;

    &::before {
      content: "";
      position: absolute;
      top: 10px;
      left: 6px;

      display: block;
      width: 10px;
      height: 1px;

      background: currentColor;
    }

    &::after {
      content: "";
      position: absolute;
      left: 11px;
      top: 6px;

      display: block;
      width: 1px;
      height: 10px;

      background: currentColor;
    }
  }
}


