.accordion {
  width: 100%;
}

.accordion__item p {
  margin: 0;
  margin-top: -5px;
  margin-bottom: 25px;
  color: #102654;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.4;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease, max-height 0.3s ease;
  will-change: opacity, max-height;
}
.accordion__item:last-child {
  border-bottom: 1px solid rgba(120, 120, 120, 0.2);
}

.accordion__item-title[aria-open=true] span {
  background: #1c3374;
}
.accordion__item-title[aria-open=true] span::after {
  width: 0;
}
.accordion__item-title[aria-open=true] span::before {
  background: #ffffff;
}
.accordion__item-title[aria-open=true] + p {
  opacity: 1;
  max-height: 9em;
  transition: all 0.3s ease;
  will-change: opacity, max-height;
}

.accordion__item-title {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 32px 0;
  outline: 0;
  list-style: none;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
  color: #102654;
  border-top: 1px solid rgba(120, 120, 120, 0.2);
  cursor: pointer;
}
.accordion__item-title::-webkit-details-marker {
  display: none;
}
.accordion__item-title span {
  position: relative;
  display: inline-block;
  width: 22px;
  height: 22px;
  padding: 11px;
  margin-left: auto;
  display: inline-block;
  width: 22px;
  height: 22px;
  border: 1px solid;
  border-radius: 22px;
}
.accordion__item-title span::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 6px;
  display: block;
  width: 10px;
  height: 1px;
  background: currentColor;
}
.accordion__item-title span::after {
  content: "";
  position: absolute;
  left: 11px;
  top: 6px;
  display: block;
  width: 1px;
  height: 10px;
  background: currentColor;
}

